import {Component, OnInit} from '@angular/core';
import {UsersService} from '../../../providers/users.service';
import {DataTableResource} from '../../../shared/components/data-table';
import {DataTableTranslations} from '../../../shared/components/data-table/components/types';
import {SweetAlertService} from 'angular-sweetalert-service';

@Component({
  selector: 'app-list-users',
  templateUrl: './list-users.component.html'
})
export class UsersComponent implements OnInit {

  itemResource: any;
  rows = [];
  itemCount = 0;
  loaded: boolean;
  defaultTranslations = <DataTableTranslations>{
    indexColumn: 'Indice',
    selectColumn: 'Seleccionar',
    expandColumn: 'Expandir',
    paginationLimit: 'Usuarios por página',
    paginationRange: 'Total de usuarios'
  };

  constructor(private usersService: UsersService, private alertService: SweetAlertService) {
    this.loaded = false;
  }

  delete(item: any) {
    this.alertService.confirm({
      title: '¿Desea eliminar este registro?',
      confirmButtonText: 'Sí',
      cancelButtonText: 'No'
    }).then((res) => {
      if (res.value) {
        item.Estatus = 2;
        this.usersService.update(item).subscribe(resp => {
          if (resp.StatusCode === 200) {
            this.alertService.success({
              title: 'Usuario Eliminado exitosamente'
            });
            this.getUsers();
          } else {
            this.alertService.warning({
              title: resp.error_description || 'No fue posible eliminar el usuario'
            });
          }
        });
      }
    });
  }

  getUsers() {
    this.usersService.get().subscribe(res => {
      if (res.Result) {
        this.loaded = true;
        this.itemResource = new DataTableResource(res.Result);
        this.itemResource.count().then(count => this.itemCount = count);

      } else {
        this.alertService.warning({
          title: res.error_description || 'No fue posible consultar la lista de usuarios'
        });
      }
    });
  }

  ngOnInit() {
    this.getUsers();
  }

  reloadItems(params) {
    console.log('PARAMS:', params);
    if (this.itemResource) {
      this.itemResource.query(params).then(rows => this.rows = rows);
    }
  }
}



