import {Component, OnDestroy, OnInit} from '@angular/core';
import {UsersService} from '../../../providers/users.service';
import {RolesService} from '../../../providers/roles.service';
import {AreaOperativaService} from '../../../providers/areaOperativa.service';
import {CompanyService} from '../../../providers/company.service';
import {BanistmoService} from '../../../providers/banistmo.service';
import {SweetAlertService} from 'angular-sweetalert-service';
import {ActivatedRoute, Router} from '@angular/router';


@Component({
  selector: 'app-edit-users',
  templateUrl: './edit-users.component.html'
})
export class EditUsersComponent implements OnInit, OnDestroy {

  user: any;
  roles = [];
  companies = [];
  areas = [];
  private sub: any;

  constructor(private usersService: UsersService,
              private rolesService: RolesService,
              private areaService: AreaOperativaService,
              private companyService: CompanyService,
              private banistmoService: BanistmoService,
              private router: Router, private route: ActivatedRoute, private alertService: SweetAlertService) {
    this.user = {};
  }

  addRol(i: number) {
    const item = this.roles.splice(i, 1)[0];
    this.user.Roles.push(item);
  }

  removeRol(i: number) {
    const item = this.user.Roles.splice(i, 1)[0];

    this.roles.push(item);
  }

  addArea(i: number) {
    const item = this.areas.splice(i, 1)[0];

    this.user.Areas.push({
      Id: item.CA_COD_AREA,
      Name: item.CA_NOMBRE
    });
  }

  removeArea(i: number) {
    const item = this.user.Areas.splice(i, 1)[0];

    this.areas.push({
      CA_COD_AREA: item.Id,
      CA_NOMBRE: item.Name
    });
  }

  addCompany(i: number) {
    const item = this.companies.splice(i, 1)[0];

    this.user.Empresas.push({
      Id: item.CE_ID_EMPRESA,
      Name: item.CE_NOMBRE
    });
  }

  removeCompany(i: number) {
    const item = this.user.Empresas.splice(i, 1)[0];

    this.companies.push({
      CE_ID_EMPRESA: item.Id,
      CE_NOMBRE: item.Name
    });
  }

  getAreas() {
    this.areaService.get().subscribe(res => {
      if (res.Result) {
        this.areas = res.Result;

        this.areas = this.areas.filter((role) => {
          return this.user.Areas.filter((per) => {
            return role.CA_COD_AREA === per.Id;
          }).length <= 0;
        });

      } else {
        this.alertService.warning({
          title: res.error_description || 'No fue posible consultar la lista de roles'
        });
      }
    });
  }

  getRoles() {
    this.rolesService.get().subscribe(res => {
      if (res.Result) {
        this.roles = res.Result;

        this.roles = this.roles.filter((role) => {
          return this.user.Roles.filter((per) => {
            return per.Id === role.Id;
          }).length <= 0;
        });


      } else {
        this.alertService.warning({
          title: res.error_description || 'No fue posible consultar la lista de roles'
        });
      }
    });
  }

  getUser(id: string) {
    this.usersService.getById(id).subscribe(res => {
      if (res.Result) {
        this.user = res.Result;

        this.getRoles();
        this.getAreas();
        this.getCompanies();
      } else {
        this.alertService.warning({
          title: res.error_description || 'No fue posible consultar el usuario'
        });
      }
    });
  }

  getCompanies() {
    this.companyService.get().subscribe(res => {
      if (res.Result) {
        this.companies = res.Result;

        this.companies = this.companies.filter((role) => {
          return this.user.Empresas.filter((per) => {
            return role.CE_ID_EMPRESA === per.Id;
          }).length <= 0;
        });
      } else {
        this.alertService.warning({
          title: res.error_description || 'No fue posible consultar la lista de roles'
        });
      }
    });
  }

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      this.getUser(params['id']);
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

  save() {
    this.saveRoles();
    this.saveAreas();
    this.saveCompanies();
  }

  saveRoles() {
    const roles = this.user.Roles.map((item) => {
      return {
        'UserId': this.user.Id,
        'RoleId': item.Id
      };
    });

    this.usersService.saveRoles({EnrolledRoles: roles, Id: this.user.Id}).subscribe(res => {
      if (res.StatusCode === 200) {
        this.alertService.success({
          title: 'Usuario actualizado exitosamente'
        });
      } else {
        this.alertService.warning({
          title: res.error_description || 'No fue posible actualizar los roles del usuario'
        });
      }
    });
  }

  saveAreas() {
    const roles = this.user.Areas.map((item) => {
      return {
        'US_ID_USUARIO': this.user.Id,
        'CA_ID_AREA': item.Id
      };
    });

    this.usersService.saveAreas({EnrolledAreas: roles, Id: this.user.Id}).subscribe(res => {
      if (res.StatusCode === 200) {
        this.alertService.success({
          title: 'Usuario actualizado exitosamente'
        });
      } else {
        this.alertService.warning({
          title: res.error_description || 'No fue posible actualizar los roles del usuario'
        });
      }
    });
  }

  saveCompanies() {
    const roles = this.user.Empresas.map((item) => {
      return {
        'US_ID_USUARIO': this.user.Id,
        'CE_ID_EMPRESA': item.Id
      };
    });

    this.usersService.saveCompanies({EnrolledEmpresas: roles, Id: this.user.Id}).subscribe(res => {
      if (res.StatusCode === 200) {
        this.alertService.success({
          title: 'Usuario actualizado exitosamente'
        });
      } else {
        this.alertService.warning({
          title: res.error_description || 'No fue posible actualizar los roles del usuario'
        });
      }
    });
  }
}



